import React, {Component, createContext } from 'react';

export const QuiverContext = createContext();

export class QuiverProvider extends Component {
  state = {
    quiverOn: true,
    quiver: []
  };
  

  render(){
    const toggleQuiver = () => {
    this.setState( {quiverOn: !this.state.quiverOn})
  };
  const addToQuiver = (board) => {
    this.setState( {quiver: {...this.state.quiver, board}});
  }
    return (
      <QuiverContext.Provider value={{...this.state, toggleQuiver, addToQuiver }}>
        {this.props.children}
      </QuiverContext.Provider>
    )
  }
}

